import React from 'react';
import { useState } from 'react';
import { useObserver } from 'mobx-react';

import { createStore } from '../../store/store';

interface Props {}

export const HomeContent: React.FC<Props> = () => {
  const [notesText, setNoteText] = useState('');
  const notesStore = createStore();

  return useObserver(() => (
    <>
      <div>
        <input
          type="text"
          value={notesText}
          onChange={(e) => setNoteText(e.target.value)}
        />
        <button onClick={() => notesStore.addNote(notesText)}>Add Note</button>
      </div>
      <ul>
        {notesStore.notes.map((note: any) => {
          return (
            <li key={note.id} onClick={() => notesStore.removeNote(note.id)}>
              {note.text}
            </li>
          );
        })}
      </ul>
    </>
  ));
};

export default HomeContent;
